body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: "Dosis", sans-serif;
}

@-webkit-keyframes appear {
  from {
    opacity: 0%;
  }
  to {
    opacity: 100%;
  }
}
@keyframes appear {
  from {
    opacity: 0%;
  }
  to {
    opacity: 100%;
  }
}

#name {
  font-family: "Dosis", sans-serif;
  margin-bottom: 2rem;
  position: relative;
  top: -60px;
  font-weight: 100;
  padding-bottom: 10px;
  padding-left: 0px;
  border-top: 2px rgb(245, 245, 116) solid;
  border-left: 2px yellow solid;
  border-right: 2px yellow solid;
  border-bottom: 2px yellow solid;
  margin-top: 10%;
  margin-bottom: 15vh;
}

.Jayansh {
  border-top: 2px rgb(245, 245, 116) solid;
  border-left: 2px yellow solid;
  border-right: 2px yellow solid;
  padding-left: 20px;
  padding-right: 20px;
  -webkit-animation-name: appear 10s;
  animation-name: appear;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}

.Bhartiya {
  border-left: 2px yellow solid;
  border-right: 2px yellow solid;
  border-bottom: 2px yellow solid;
  padding-left: 20px;
  padding-right: 20px;
  margin-bottom: 20px;
  -webkit-animation-name: appear 6s;
  animation-name: appear;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}

.graySidebar {
  background-color: #373737;
  min-height: 100vh;
  height: 100%;
  color: white;
  position: fixed !important;
  z-index: 2;
}

.graySidebar img {
  border-spacing: 5px;
  box-shadow: black;
  border-radius: 50%;
  -webkit-animation-name: appear 4s;
  animation-name: appear;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
}

.social-links {
  -webkit-animation-name: appear 5s;
  animation-name: appear;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
}

.navigation {
  -webkit-animation-name: appear 6s;
  animation-name: appear;
  -webkit-animation-duration: 6s;
          animation-duration: 6s;
}

.whiteCarryover {
  background-color: #2e2e2e;
  color: white;
  margin-left: auto;
}

.whiteCarryover div h1 {
  font-size: 60px;
  -webkit-animation-name: appear 4s;
  animation-name: appear;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
}

.introhead {
  border-bottom: yellow solid 2px;
  transition: appear;
  transition-duration: 4s;
}

.hey {
  -webkit-animation-name: appear 4s;
  animation-name: appear;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
}

.swe {
  -webkit-animation-name: appear 6s;
  animation-name: appear;
  -webkit-animation-duration: 6s;
          animation-duration: 6s;
}

.short-intro,
.short-intro-bio {
  -webkit-animation-name: appear 8s;
  animation-name: appear;
  -webkit-animation-duration: 8s;
          animation-duration: 8s;
}

.summer,
.summer-bio {
  -webkit-animation-name: appear 9s;
  animation-name: appear;
  -webkit-animation-duration: 9s;
          animation-duration: 9s;
}
.aboutMe,
.aboutMeBio {
  -webkit-animation-name: appear 10s;
  animation-name: appear;
  -webkit-animation-duration: 10s;
          animation-duration: 10s;
}
ul li a {
  color: white;
  font-size: 1.2em;
}

ul li a:hover {
  color: yellow;
  transition: color 1s;
}
ul {
  margin: 0px;
  padding: 0px;
  text-align: center;
}

ul li {
  display: inline-block;
  margin: 3% 3% 0 3%;
  padding: 0px;
  opacity: 0.85;
}

#navigation li a {
  text-decoration: none;
}

.home-auto .whiteCarryover {
  height: auto !important;
}

@media (max-width: 1200px) {
  .graySidebar {
    min-height: 100vh;
    height: 100%;
    position: static !important;
    z-index: 2;
  }
  .whiteCarryover {
    margin-left: 0px;
    width: 100vh;
  }
}

@media (max-width: 480px) {
  .graySidebar {
    min-height: auto;
    padding-bottom: 20px;
  }
  .graySidebar.justify-content-between {
    justify-content: normal !important;
  }
  .navigation {
    display: none;
  }
}

@-webkit-keyframes logoAppear {
  from {
    opacity: 0%;
  }
  to {
    opacity: 100%;
  }
}

@keyframes logoAppear {
  from {
    opacity: 0%;
  }
  to {
    opacity: 100%;
  }
}

#topLogo {
  font-size: 30px;
  display: flex;
  flex-direction: row;
  font-weight: 900;
  color: white;
  cursor: pointer;
  text-decoration: none;
  -webkit-animation-name: logoAppear 2s;
  animation-name: logoAppear;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  padding: 10px 0 10px 30px !important;
}
.sticky-sidebar svg{
  display: none;
}
.sticky-sidebar nav{
  background: #2e2e2e !important;
}
.sticky-sidebar nav h1{
  background: #373737;
  padding: 0 !important;
}
.sticky-sidebar nav ul li{
  background: transparent !important;
  display: block;
  text-align: left;
  font-size: 24px;
  padding: 5px 20px !important;
}

@media (max-width: 480px) {
  .whiteCarryover{
    height: 200vh;
  }
  .cover_pic{
    margin-top: 70px;
  }
  .fixed-on-mobile{
    position: fixed;
    width: 100%;
    background: #373737;
    left: 0;
    z-index: 9;
  }
  .sticky-sidebar svg{
    position: absolute;
    right: 30px;
    top: 20px;
    display: block;
  } 
}
